.support {
  color: $g-col-blue;

  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(24px, 36px);
    font-weight: 700;
  }

  &__subtitle {
    margin-top: 0;
    margin-bottom: 10px;
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;
    color: $g-col-gray-strouk;
  }

  &__phone {
    margin-bottom: 30px;
    @include fluidFontSize(26px);
    font-weight: 700;
  }

  &__link {
    display: block;

    &:nth-of-type(n + 2) {
      margin-top: 15px;
    }
  }

  &__soc {
    display: inline-block;
    & > * {
      display: block;
    }
  }

  .icon {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  }
}

@media (max-width: $breakpoint-small) {

  .support {
    &__wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__phone {
      margin-bottom: 0;
    }

    .icon {
      margin-bottom: 15px;
      width: 29px;
      height: 29px;
    }
  }
}