.order {
  margin-top: 30px;
  margin-bottom: 90px;

  &__content {    
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 120px;
    background-color: $g-col-white;
    border-radius: 10px;
  }

  &__form {
    padding-right: 12.5%;
  }

  &__title {
    @include fluidFontSize(22px, 26px);
    margin-bottom: 40px;
    font-weight: 700;
    color: $g-col-blue;
  }

  &__item {
    &:not(:first-child) {
      margin-top: 50px;
    }
  }

  &__subtitle {
    position: relative;
    @include fluidFontSize(18px, 26px);
    font-weight: 700;
    color: $g-col-blue;

    &:before {
      position: absolute;
      content: "";
      top: 10px;
      left: -30px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $g-col-blue;
    }
  }

  &__text {
    @include font-size('p1');
    line-height: 1.5;
    letter-spacing: 0.01em;
  }
}

@media (max-width: $breakpoint-large) {
  .order {
    &__form {
      padding-right: 5%;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .order {

    &__content {
      padding-bottom: 30px;
    }

    &__form {
      padding-right: 0;
      padding-bottom: 60px;
    }

    &__list {
      padding-left: 25px;
    }


    &__item {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }

    &__subtitle {
      margin-bottom: 15px;
      &:before {
        top: 6px;
        left: -25px;
      }
    }
  }
}

@media (max-width: $breakpoint-small) {

  .order {
    margin-top: 15px;
    margin-bottom: 60px;

    &__content {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}