.save-bk {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 1100;
  display: none;
  background-color: $g-col-white;

  &.active {
    display: block;
  }

  &__wrap {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
  }

  .btn {
    max-width: 260px;
    width: 100%;
    margin-right: 70px;
  }
}

@media (max-width: $breakpoint-small) {


  .save-bk {
    &__wrap {
      padding-left: 20px;
      padding-right: 20px;
    }
    .btn {
      max-width: 130px;
      margin-right: 30px;
    }
  }
}