.pers-headline {
  margin-top: 25px;
  margin-bottom: 25px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  color: $g-col-blue;

  &__wrap-title {
    p {
      display: inline-block;

      &:nth-child(2) {
        margin-left: 45px;
      }
    }

    a {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__title {
    margin: 0;
    @include fluidFontSize(22px, 26px);
    font-weight: 700;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;


  }

  &__link {

    &:nth-child(2) {
      margin-left: 40px;
    }

    @include font-size('p1');
    font-family: $g-font-extra;
    text-decoration: underline;

    &_pen {
      position: relative;
      padding-right: 50px;
      &:before {
        position: absolute;
        content: "";
        bottom: 2px;
        right: 10px;
        width: 21px;
        height: 17px;
        background: url(../img/pen.svg) no-repeat center;
        background-size: 21px 17px;
      }
    }
  }

}

@media (max-width: $breakpoint-medium) {

  .pers-headline {
    &__link {

      &_pen {
        padding-right: 40px;

        &:before {
          bottom: 1px;
          width: 15px;
          height: 13px;
          background-size: 15px 13px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-small) {

  .pers-headline {
    margin-top: 20px;
    margin-bottom: 15px;
    display: block;
    padding-left: 20px;
    padding-right: 20px;

    &__wrap {
      margin-top: 15px;
    }
  }
}