.login  {
  margin-top: 60px;
  margin-bottom: 90px;
  padding-top: 40px;
  padding-bottom: 100px;
  background-color: #fff;
}

@media (max-width: $breakpoint-small) {
  .login {
    margin-top: 15px;
    margin-bottom: 40px;
    padding: 0;
    background-color: transparent;

    &__content {
      padding-top: 20px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 60px;
      background-color: #fff;
      border-radius: 5px;
    }
  }
}