.search-result {
  margin-top: 60px;

  &__item {
    margin-bottom: 30px;
    position: relative;
    padding: 30px 0;
    border-radius: 10px;
    background-color: #fff;

    &:before {
      position: absolute;
      content: "";
      top: 50%;
      left: 20%;
      transform: translateY(-50%);
      width: 1px;
      height: 30px;
      background-color: $g-col-gray-strouk;
    }
  }

  &__section {
    position: relative;
    padding-left: 30px;
    padding-right: 10px;
    margin-right: 40px;
    color: $g-col-blue;
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;

    span {
      &:before {
        content: " “"
      }

      &:after {
        content: "”"
      }
    }
  }

  &__wrap {
    margin-right: 30px;
    max-width: 720px;
  }

  &__title {
    @include fluidFontSize(18px, 26px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__text {
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;

    p {
      margin-bottom: 0;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .search-result {

    &__item {
      padding-top: 20px;
      padding-left: 20px;
      padding-right: 20px;

      &:before {
        content: none;
      }
    }

    &__section {
      padding: 0;
      padding-bottom: 10px;

      &:before {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        width: 30px;
        height: 1px;
        background-color: $g-col-gray-strouk;
      }
    }

    &__wrap {
      margin-right: 0;
    }

    .uk-grid-margin {
      margin-top: 15px;
    }

    &__title {
      margin-bottom: 15px;
    }
  }

}

@media (max-width: $breakpoint-small) {

  .search-result {
    margin-top: 30px;
    margin-bottom: 30px;
    &__item {
      margin-bottom: 10px;
    }
  }
}