.rates {

  .uk-grid {
    margin-left: -10px;

    & > * {
      padding-left: 10px;
    }
  }

  &__item {
    padding-top: 30px;
    padding-bottom: 60px;
    border-radius: 10px;
    background-color: #fff;
  }

  &__title {
    padding-left: 30px;
    padding-right: 10px;
    margin-bottom: 30px;
    @include fluidFontSize(26px, 36px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__table {
    width: 100%;
    border-collapse: collapse;
    p {
      margin: 0;
  
      &:not(:first-of-type) {
        margin-top: 5px;
      }
    }
  }

  &__tr {
    display: flex;
    align-items: center;
    padding-left: 30px;
    padding-right: 10px;
  }

  &__th {
    padding: 0;
    padding-right: 10px;
    margin-bottom: 20px;
    text-align: left;
    font-weight: 400;
    @include fluidFontSize(12px, 18px);
    letter-spacing: 0.01em;
    line-height: 1.5;
  }

  &__td {
    padding: 0;
    padding-right: 10px;
    color: $g-col-blue;
  }

  &__td_flag {
    p {
      margin-left: 2px;
    }

    p:not(:first-of-type) {
      display: none;
      text-transform: lowercase;
    }
  }

  &__td_flag, &__td_title {
    @include fluidFontSize(16px);
    text-transform: uppercase;

    p:not(:first-of-type) {
      @include fluidFontSize(11px, 13px);
      color: $g-col-red;
    }
  } 

  &__td_title {
    p:not(:first-of-type) {
      text-transform: initial;
      font-weight: 700;
      font-family: $g-font-extra;
    }
  }

  &__td_flag, &__th_flag {
    width: 15%;
  }

  &__td_title, &__th_title {
    padding-right: 15px;
    width: 21%;
  }

  &__td_num, &__th_num {
    width: 16%;
  }

  &__td_num {
    line-height: 1;
    @include fluidFontSize(14px, 26px);
    font-weight: 500;
  }



  &__flag {
    margin-bottom: 10px;

    img {
      width: 34px;
      height: 34px;
      overflow: hidden;
      border-radius: 50%;
    }
  }

  &__tr_body {
    padding-top: 17px;
    padding-bottom: 17px;

    &:nth-of-type(-n + 2) {
      
      background-color: #EBEBEB;
    }
  }
}

@media (max-width: $breakpoint-large) {

  .rates {
    &__th_title, &__td_title {
      display: none;
    }

    &__td_flag, &__th_flag {
      width: 32%;
    }

    &__td_num, &__th_num {
      width: 17%;
    }

    &__td_flag {
      display: flex;
      align-items: center;

      p:not(:first-of-type) {
        display: block;
        margin-top: 0;
      }
    }

    &__flag {
      margin-bottom: 0;
      margin-right: 10px;

      img {
        width: 26px;
        height: 26px;
      }
    }
  }
}

@media (max-width: $breakpoint-small) {

  .rates {

    .uk-grid-margin {
      margin-top: 20px;
    }

    &__table {
      margin-bottom: 30px;
    }

    &__title {
      margin-bottom: 45px;
      padding-left: 20px;
    }

    &__item {
      padding-bottom: 20px;
    }

    &__th {
      margin-bottom: 7px;
    }

    &__tr {
      padding-left: 20px;
    }

    &__td, &__th {
      padding-right: 5px;
    }
    &__tr_body {
      padding-top: 13px;
      padding-bottom: 13px;
    }

    &__td_flag {
      font-weight: 700;
    }

    &__more {
      margin: 0 20px;
    }

    .btn {
      width: 100%;
      color: $g-col-white;

      &:hover {
        background-color: $g-col-green-hov;
      }

    }
  }
}