//exchange page
.rates-page {

  margin-top: 90px;
  margin-bottom: 90px;

  &__wrap {
    margin-bottom: 60px;
  }

  &__title {
    margin-bottom: 15px;
    @include fluidFontSize(24px, 26px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__text {
    margin: 0;
    @include font-size('p1');
    letter-spacing: 0.01em;
  }
}

