.news-el {
  margin: -30px;
  padding: 30px;
  .uk-grid {

    & > * {
      display: flex;
    }
  }

  &__item {
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
    transition: $g-anime;

    a {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
    }
  
    &:hover {
      transform: translateY(-20px);
      box-shadow: $g-shadow;
    }
  }

  &__wrap {
    padding-left: 30px;
    padding-right: 30px;
  }

  &__title {
    padding-top: 20px;
    @include font-size('p2');
    color: $g-col-blue;
  }

  &__date {
    color: $g-col-green;
    @include font-size('p3');
    font-family: $g-font-extra;
  }
  
  &__img {
    // display: flex;
    // height: 230px;
    img {
      width: 100%;
      // object-fit: cover;
      // object-position: top;
    }
  }

  &__vert {
    display: block;
    position: static;

    & > li:nth-child(n + 2) {
      margin-top: 30px;
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .news-el {
    &__wrap {
      padding-left: 10px;
      padding-right: 10px;
    }

    &__title {
      padding-top: 10px;
    }

    &__vert {
      display: flex;
      position: relative;
  
      & > li:nth-child(n + 2) {
        margin-top: 0;
      }
    }
  }
}


@media (max-width: $breakpoint-small) {

  .news-el {
    margin: -10px;
    padding: 10px;

    .uk-grid {
      margin-left: -10px;
  
      & > * {
        padding-left: 10px;
      }
    }

    &__item {
      &:hover {
        transform: none;
      }
    }

    &__img {
      // height: 180px;
    }
  }
}
