.useful {
  margin-top: 50px;
  margin-bottom: 120px;
  padding-top: 45px;
  padding-bottom: 45px;
  border-radius: 10px;
  background-color: $g-col-white;
  color: $g-col-blue;

  &__content {
    align-items: center;
  }

  &__text {
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;
    text-align: center;
  }

  &__btn-wrap {
    display: flex;
    justify-content: center;
  }
  .btn {
    max-width: 260px;
    width: 100%;
    @include font-size('p12');
    text-transform: uppercase;
    font-weight: 700;
    color: $g-col-white;

    &:first-child {
      margin-right: 30px;
    }

    span {
      margin-left: 20px;
      margin-right: 20px;
    }
    
    .icon {
      fill: $g-col-white;
    }
  }

  .btn_green {
    &:hover {
      background-color: $g-col-green-hov;
    }

  }

  .link {
    @include fluidFontSize(18px);

    span {
      text-decoration: underline;
      margin-right: 5px;
    }

    &:hover {
      .icon {
        fill: $g-col-green;
      }
    }

    .icon {
      fill: $g-col-blue;
      transition: $g-anime;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .useful {
    padding-top: 40px;
    padding-bottom: 55px;
    padding-left: 20px;
    padding-right: 20px;
    &__btn-wrap {
      display: block;
      margin-bottom: 30px;
    }

    .btn {
      margin-bottom: 10px;
      margin-left: auto;
      margin-right: auto;


      &:first-child {
        margin-right: auto;
      }
    }
  }
}

@media (max-width: $breakpoint-small) {
  .useful {
    margin-bottom: 50px;

  }
}