.faq-page {
  margin-top: 60px;
  margin-bottom: 120px;
  margin-left: -30px;
  display: flex;

  &__el {
    padding-left: 30px;
    width: 75%;

    &:last-of-type {
      width: 25%;
    }
  }
}

.faq {

  &__list {
    & > :nth-child(n+2) {
      margin-top: 10px;
    }
  }
  &__item {
    padding-left: 30px;
    padding-right: 20px;
    background-color: $g-col-l-green;
    transition: $g-anime;
    border-radius: 5px;

    &.uk-open {
      padding-top: 5px;
      padding-bottom: 60px;
      background-color: #fff;
    }
  }

  &__title {
    width: 100%;
    padding: 25px 0;
    color: $g-col-blue;
    @include font-size('h3');
    font-weight: 700;
    border: none;
    background: transparent;
    text-align: left;
    transition: $g-anime;

    &:before {
      content: none;
    }
    &:after {
      top: 30px;
      transform: none;
      right: 10px;
    }
    &:hover {
      color: $g-col-green;
    }

    &:focus {
      color: $g-col-blue;
    }
  }

  &__text {
    max-width: 820px;
    margin-top: 5px;
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;
  }
}

.fast-lk {
  padding-top: 15px;
  padding-right: 10px;
  @include fluidFontSize(18px);
  color: $g-col-blue;
  letter-spacing: 0.01em;

  &__link {
    display: block;
    padding-top: 14px;
    padding-bottom: 14px;
    
    &:hover {
      text-decoration: underline;
    }
  }
}

.faq-form {
  margin-top: 120px;
  margin-bottom: 90px;
  padding-top: 30px;
  padding-bottom: 100px;
  border-radius: 5px;
  background-color: $g-col-white;
  
}

.form-cont {
  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(24px, 36px);
    font-weight: 700;
    color: $g-col-blue;
  }

  .control-label {
    display: none;
  }

  .form-group {
    margin-bottom: 30px;
  }
  .input {
    height: 52px;
    @include fluidFontSize(18px);
    background-color: transparent;
    padding-left: 20px;
  }

  .textarea {
    color: $g-col-blue;
    @include fluidFontSize(18px);
    padding-top: 12px;
    padding-left: 20px;
    width: 100%;
    height: 166px;
    border-radius: 5px;
    resize: none;
  }

  .btn {
    color: $g-col-white;
    max-width: 240px;
    width: 100%;

    &:hover {
      background-color: $g-col-green-hov;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .faq-page {
    margin-top: 15px;
    flex-direction: column-reverse;

    &__el {
      width: 100%;

      &:last-of-type {
        width: 100%;
        margin-bottom: 30px;
      }
    }
  }

  .faq-form {
    padding-top: 20px;
  }
  .form-cont {
    padding-bottom: 20px;
  }
}

@media (max-width: $breakpoint-small) {
  .fast-lk {
    padding-left: 10px;
    padding-right: 10px;
  }

  .faq-page {
    margin-bottom: 50px;
  }

  .faq {
    &__item {
      padding-left: 10px;
      padding-right: 10px;
    
      &.uk-open {
        padding-bottom: 30px;
      }
    }
    &__title {
      padding-top: 20px;
      padding-bottom: 20px;
      padding-right: 20px;

    }
  }

  .faq-form {
    margin-top: 50px;
    margin-bottom: 20px;
    padding: 0;
    background-color: transparent;

    &__content {
      padding-top: 20px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 60px;
      background-color: $g-col-white;
      border-radius: 5px;
    }
  }

  .form-cont {
    .control-label {
      display: block;
      margin-bottom: 10px;
      font-size: 18px;
      color: $g-col-blue;
    }

    .form-group {
      margin-bottom: 20px;
    }

    .uk-grid-margin {
      margin-top: 0;
    }

    .btn {
      max-width: 100%;
    }
  }


}