.pers-nav {
  margin-top: 30px;
  margin-bottom: 20px;
  color: $g-col-blue;
  letter-spacing: 0.01em;

  @include font-size('p1');

  &_tabs {
    margin-bottom: 0;
  }

  &__content {
    position: static;
    display: block;
    padding: 0;
    box-shadow: none;
    background-color: transparent;

    &.uk-open {
      display: block;
    }
  }
  
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: -30px;

    & > li {
      padding-left: 30px;
    }
  }

  &__list_tabs {
    margin: 0;
    & > li {
      padding-left: 0;
      width: 15%;
    }

    li.uk-active {
      background-color: $g-col-white;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      .pers-nav__link_tabs {
        cursor: default;
        pointer-events: none;
        color: $g-col-green;
      }
    }
  }

  &__link {
    &:hover {
      color: $g-col-green;
    }

    &.active {
      cursor: default;
      pointer-events: none;
      color: $g-col-green;
    }
  }

  &__link_tabs {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 70px;
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
  }

  &__btn {
    display: none;
  }
}

@media (max-width: $breakpoint-medium) {
  .pers-nav {
    position: relative;

    &_tabs {
      margin-bottom: 20px;
    }

    &__content {
      position: absolute;
      background-color: $g-col-white;
      box-shadow: $g-shadow;
      display: none;
      border-radius: 5px;
      padding-top: 50px;
      padding-left: 30px;
      padding-right: 10px;
    }
    &__list {
      display: block;
      margin-left: 0;


      & > li {
        padding-left: 0;
      }

      
      & > li:first-of-type {
        padding-top: 10px;
        border-top: 1px solid $g-col-gray-strouk;
      }

      & > li:last-of-type {
        padding-bottom: 10px;
      }
    }

    &__link {
      display: block;
      padding: 15px 20px;
    }

    &__btn {
      position: relative;
      padding-left: 0;
      padding-right: 45px;
      z-index: 1050;
      display: block;
      background-color: transparent;
      border: none;
    }


    &__list_tabs {
      margin: 0;
      & > li.uk-active {
        border-top-left-radius: unset;
        border-top-right-radius: unset;
      }

      & > li {
        padding-left: 0;
        width: 100%;
      }
    }

    &__link_tabs {
      display: block;
      min-height: auto;
      padding-left: 0;
      padding-right: 0;
      text-align: left;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .pers-nav {
    margin-top: 20px;
    padding-left: 0;
    padding-right: 20px;

    &__content {
      padding-left: 20px;
    }

    &__btn {
      padding-left: 20px;
    }

    &__btn_tabs {
      padding-left: 0;
    }
  }
}