.header {

  &__wrap {
    margin-left: -30px;
  }

  &__top-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 10px;
  }

  &__bottom-menu {
    margin-right: -30px;
    padding-right: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $g-col-white;
  }

  &__menu {
    margin-right: 40px;
    padding-left: 30px;
    flex-grow: 1;
  }

  &__phone-drop {
    margin-right: 15px;
  }

  &__lang {
    margin-left: 40px;
  }

  &__search {
    max-width: 250px;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
    .input {
      @include font-size('p6');
    }

    .search__btn {
      right: 15px;
    }
  }
}

.logo {
  @include fluidFontSize(12px, 26px);
  font-weight: 500;
  text-align: center;
  color: $g-col-blue;
  &__img {
    margin: 0 auto;
    width: 45px;
    height: 45px;
  }

  &__title {
    margin-top: 15px;
    margin-bottom: 0;
  }
}

.menu {
  @include font-size('p7');
  font-weight: 500;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: $g-col-blue;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    display: block;
    padding-top: 15px;
    padding-bottom: 15px;
    
    .icon {
      margin-right: 5px;
    }

    &:hover {
      color: $g-col-green;
    }
  }
}

.phone-drop {
  padding-right: 25px;
  color: $g-col-blue;

  &__btn {
    padding-top: 10px;
    padding-bottom: 10px;
    @include font-size('p8');
    font-weight: 500;
    letter-spacing: 0.3px;
    text-transform: uppercase;
    background: transparent;
    border: none;

    &.uk-open.arrow-up {
      &:before {
        position: absolute;
        z-index: 1030;
        content: "";
        top: 0;
        right: 0;
        width: 145%;
        height: 100%;

      }
      &:after {
        z-index: 1030;
      }
    }
  }

  &__popup {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 15px;
    padding-bottom: 30px;
    width: 300px;
  }

  &__title {
    @include font-size('h5');
    font-weight: 500;
  }
  &__text {
    margin-top: 10px;
    margin-bottom: 10px;
    @include font-size('p1');
    letter-spacing: 0.01em;
  }

  &__social {
    margin-top: 30px;
  }
}

.lang {
  @include font-size('p9');
  color: $g-col-blue;

  &__btn {
    position: relative;
    margin-bottom: 15px;
    padding-left: 15px;
    padding-right: 15px;
    display: block;
    border: none;
    background: transparent;

    &:before {
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      width: 0;
      height: 2px;
      transform: translateX(-50%);
      border-radius: 4px;
      background-color: $g-col-green;
    }

    &.active {
      margin-bottom: 0;
      padding-left: 5px;
      padding-right: 5px;
      &:before {
        width: 22px;
      }
    }

    &:hover:before {
      width: 22px;
    }

    &.uk-open {
      z-index: 1050;
    }
  }

  &__popup {
    min-width: auto;
    padding: 0;
    padding-top: 50px;
    border-radius: 5px;
  }
}

.submenu {
  padding-left: 30px;
  padding-right: 30px;
  flex-grow: 1;
  max-width: 760px;
  @include fluidFontSize(13px);
  color: $g-col-blue;

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    display: block;
    padding: 20px 0;
    &:hover {
      color: $g-col-green;
    }
  }
}



@media (max-width: $breakpoint-large) {

  .header {

    &__menu {
      margin-right: 20px;
    }
    &__search {
      max-width: 20%;
    }

    &__lang {
      margin-left: 20px;
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .header {
    &__wrap {
      margin-left: 0;
    }

    &__top-menu {
      padding: 7px 0;
    }

    .logo {
      flex-grow: 1;
    }

    &__lang {
      margin-left: 30px;
    }


    &__mob-menu {
      background-color: #fff;
    }
  }

  .logo {
    display: flex;
    align-items: center;

    &__img {
      margin: 0;
      margin-right: 10px;
    }

    &__title {
      margin: 0;
    }
  }


  .mob-menu {
    min-height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    
    &__burger {
      position: relative;
      padding-left: 40px;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 16px;
      background-color: transparent;
      border: none;
      color: $g-col-blue;

      &:before, &:after {
        position: absolute;
        content: "";
        top: 3px;
        left: 0;
        width: 24px;
        height: 2px;
        background-color: #223554;
        box-shadow: 0 7px 0 0 #223554;
        transition: $g-anime;
      }

      &:after {
        top: 17px;
        background-color: #223554;
        box-shadow: none;
      }

      &.uk-open {
        z-index: 1050;
        &:before {
          top: 50%;
          box-shadow: 0 0px 0 0 #223554;
          transform: translateY(-50%) rotate(45deg);
        }
  
        &:after {
          top: 50%;
          transform: translateY(-50%) rotate(-45deg);
        }
      }
    }

    &__link {
      font-weight: 500;
      font-size: 12px;
      text-transform: uppercase;
      color: $g-col-blue;

      span {
        text-decoration: underline;
      }

      .icon {
        margin-right: 12px;
      }

      &:hover {
        span {
          text-decoration: none;
        }
      }
    }

    &__popup {
      padding-left: 30px;
      padding-right: 0;
      padding-bottom: 0;
      padding-top: 60px;
    }

    &__wrap {
      display: flex;
    }


    &__btn-search {
      position: absolute;
      width: 30px;
      height: 30px;
      padding: 0;
      top: 15px;
      right: 30px;
      background-color: transparent;
      border: none;
    }
  }

  .menu {
    max-width: 140px;
    width: 100%;
    padding-right: 20px;
    &__list {
      display: block;
    }

    &__link {
      background-color: transparent;
      border: none;
      position: relative;
      padding-left: 30px;
      font-size: 14px;
      font-weight: 500;
      text-transform: uppercase;
      width: 100%;
      text-align: left;

      .icon {
        position: absolute;
        left: 0;
        top: 15px;
      }

      &:hover {
        &:after{
          border-color: $g-col-green;
        }
      }

      &.active {
        color: $g-col-green;

        &:after{
          border-color: $g-col-green;
        }
      }
    }
  }

  .submenu {
    padding-right: 30px;
    padding-bottom: 20px;
    background-color: #F6F6F6;
    &__list {
      display: block;
    }

    &__link {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .header {
    &__lang {
      margin-right: 10px;
    }

    &__top-menu {
      padding-left: 10px;
    }

    &__mob-menu {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .mob-menu {

    &__popup {
      padding-left: 20px;
    }

    &__btn-search {
      right: 20px;
    }
  }

  .submenu {
    padding-right: 20px;
  }
}

.preview-menu {
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: #fff;
}