.form {

  // form grid
  &__grid {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: -30px;
    &:nth-child(n + 2) {
      margin-top: 20px;
    }
  }

  &__grid-el {
    padding-left: 30px;

    &:last-child {
      margin-left: auto;
    }
  }
  // form grid end

  //tabs
  &_tabs {
    .btn {
      margin-top: 30px;
    }
  }
  //tabs end

  .form-group {

    &_flex {
      display: flex;
      align-items: center;
    }
  }

  &__title {
    margin: 0;
    color: $g-col-blue;
    @include font-size('p1');

    letter-spacing: 0.01em;
  }

  &__block:nth-child(n + 2) {
    margin-top: 40px;
  }

  .form-group_attach {
    display: flex;
  }

  .form-group__short {
    max-width: 260px;
  }

  &__attach {
    margin-left: 5px;
    margin-right: 40px;
    @include font-size('p1');
    letter-spacing: 0.01em;
    color: $g-col-blue;
    text-decoration: underline;
    transition: $g-anime;

  }
  
  .valid, .invalid {
    position: relative;

    @include fluidFontSize(14px);
    &:before {
      position: absolute;
      content: "";
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 32px;
      height: 32px;
      background: url(../img/valid.svg) no-repeat center;
      background-size: 32px;
    }
  }

  .invalid {
    &:before {
      background-image: url(../img/invalid.svg);
    }
  }

  &__file-name {
    position: relative;
    padding-right: 25px;
    @include font-size('p1');
    letter-spacing: 0.01em;
    text-decoration: underline;

    &:before, &:after {
      position: absolute;
      content: "";
      top: 50%;
      right: 0;
      transform: translateY(-50%) rotate(-45deg);
      width: 19px;
      height: 3px;
      background-color: #D42424;
    }

    &:after {
      transform: translateY(-50%) rotate(45deg);
    }
  }

  .help-block {
    width: 100%;
  }

  .control-label {
    color: $g-col-blue;
    // @include fluidFontSize(18px);
    @include font-size('p1');
    letter-spacing: 0.01em;
  }

  &__select, .input {
    height: 52px;
    width: 100%;
    // @include fluidFontSize(18px);
    @include font-size('p1');

    background-color: $g-col-white;
  }

  &__terms {

    .control-label {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      color: $g-col-blue;
    }

    a {
      color: $g-col-green;
      text-decoration-line: underline;
    }

    .input {
      position: relative;
      display: inline-block;
      padding: 0;
      height: 31px;
      min-width: 31px;
      min-height: 31px;
      width: 31px;
      margin-left: 0;
      margin-top: 0;
      margin-right: 8px;
      overflow: hidden;
      vertical-align: middle;
      background-color: transparent;
      border: 1px solid $g-col-gray-strouk;
      -webkit-appearance: none;
      -moz-appearance: none;
      border-radius: 5px;

      &:checked:before {
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 20px;
        height: 20px;
        background: $g-col-green;
        border-radius: 5px;
      }
    }
    
  }

  .btn {
    max-width: 240px;
    width: 100%;
    color: $g-col-white;

    &:hover {
      background-color: $g-col-green-hov;
    }
  }

  &__wrap-select {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      right: 12px;
      top: 50%;
      z-index: 0;
      transform: translateY(-50%);
      display: inline-block;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 8px solid $g-col-text;
    }
  }

  &__select {
    position: relative;
    z-index: 1;
    width: 100%;
    padding-left: 30px;
    border-radius: 5px;
    border-color: $g-col-gray-strouk;
    text-transform: uppercase;
    color: $g-col-blue;
    background-color: transparent;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &__text {
    margin-top: 40px;
    font-family: $g-font-extra;
    @include fluidFontSize(14px);
    letter-spacing: 0.01em;
    line-height: 1.5;
  }

  &__currency {
    @include fluidFontSize(12px, 18px);
  }

  &__forg-pass {
    display: block;
    margin-top: 20px;
    margin-bottom: 45px;
    @include fluidFontSize(13px, 12px);
    text-decoration: underline;
    font-family: $g-font-extra;
    text-align: center;
    color: $g-col-blue;
  }

  &__link {
    text-align: left;
    letter-spacing: 0.01em;
    text-decoration: underline;
    color: $g-col-blue;
    @include font-size('p1');

    &:hover {
      text-decoration: none;
    }
  }

  &__user-data {
    text-align: right;
    margin: 0;
    
    &.valid, &.invalid {
      min-width: 32px;
      min-height: 32px;

      &:before {
        top: 50%;
        left: auto;
        right: 0;
        transform: translateY(-50%);
      }
    }
  }

  &__user-email {
    word-wrap: break-word;
  }
}

// grid form size
.form {
  &__grid-el_2-8 {
    &:first-child {
      width: 20%;
    }

    &:last-child {
      width: 80%;
    }
  }

  &__grid-el_3-7 {
    &:first-child {
      width: 30%;
    }

    &:last-child {
      width: 70%;
    }
  }

  &__grid-el_4-6 {
    &:first-child {
      width: 40%;
    }

    &:last-child {
      width: 60%;
    }
  }
}
// grid form size end

// order page 
.form {

  &_order {
    .btn {
      max-width: 260px;
    }
    .form__wrap-select {
      margin-right: 40px;
    }
  }

  &__block_order {
    .input, .form__wrap-select {
      width: 50%;
      min-width: 50%;
    }
  }

  &__el_order {
    .form-group {
      display: flex;
      align-items: center;
    }
  }
}
// order page end

// login page 
.form {

  &_login {
    margin-left: auto;
    margin-right: auto;
    width: 38%;

    .btn {
      max-width: 100%;
    }
  }

  &__block_login {
    margin-bottom: 30px;
  }
}
// login page end


// personal page 4 Edit
.form {
  &_edit {
    .form__attach {
      display: block;
      padding-top: 20px;
      padding-bottom: 20px;
      padding-left: 50px;
    }
  }

  &__grid-el_edit-wide {
    display: flex;
    .form-group {
      width: 60%;
    }
    .form__link {
      padding-left: 40px;
      width: 40%;
    }

    &:first-child {
      width: 30%;
    }

    &:last-child {
      width: 70%;
    }
  }
}
// personal page 4 Edit end

// personal page 7 Edit
.form {
  &_horiz {

    .uk-grid {
      margin-left: -60px;
    }

    .uk-grid > * {
      padding-left: 60px;
    }
  }
}
// personal page 7 Edit end

// personal page 9 Edit
.form {

  &_sum {
    padding-top: 20px;
    border-top: 1px solid #e5e5e5;
    .btn {
      max-width: 100%;
      margin-top: 20px;
    }
  }
  &__grid-el_sum {
    width: 100%;
  }
}
// personal page 9 Edit end

// personal page 10 Edit
.form {

  &_card-data {

    .form-group {
      max-width: 340px;
    }
    .btn {
      margin-top: 30px;
    }
  }

  &__grid_card-data {
    .form-group, .form__wrap-select {
      max-width: 170px;
    }
  }
}
// personal page 10 Edit end

@media (max-width: $breakpoint-large) {

  .form {
    // order page 
    &_order {

      .form__wrap-select {
        margin-right: 20px;
      }
    }
    // order page end
  }
}

@media (max-width: $breakpoint-medium) {


  // login page 
.form {

  &_login {
    width: 70%;
  }
}
// login page end


// personal page 7 Edit
.form {
  .uk-grid {
    margin-left: -30px;
  }

  .uk-grid > * {
    padding-left: 30px;
  }
}
// personal page 7 Edit end

}

@media (max-width: $breakpoint-small) {
  .form {
    &__grid {
      display: block;
      margin-left: 0;
    }

    &__grid-el {
      padding-left: 0;
    }

    .input {
      padding-left: 15px;
    }

    .form-group_attach {
      flex-direction: column-reverse;

      & > div {
        margin-top: 10px;
      }

      & > div:first-child {
        margin-top: 20px;
      }
    }
    .form-group__short {
      max-width: 100%;
    }
    &__attach {
      margin-left: 0;

    }
    
    .valid, .invalid {
      &:before {
        width: 16px;
        height: 16px;
        background-size: 16px;
      }
    }

    &__terms {

      .control-label {
        align-items: flex-start;
        margin-bottom: 20px;
      }
  
      .input {
        height: 20px;
        width: 20px;
        min-width: 20px;
        min-height: 20px;
        margin-right: 30px;

        &:checked:before {
          width: 10px;
          height: 10px;
        }
      }
      
    }
  
    &__wrap-title {
      display: flex;
      align-items: baseline;
      justify-content: space-between;
    }

    &__select {
      padding-left: 15px;
    }

    .btn {
      max-width: 100%;
    }

    &__user-data {
      &.valid, &.invalid {
        min-width: 16px;
        min-height: 16px;
      }
    }
  }



  .form {
    &__grid-el {
      &:first-child {
        width: 100%;
      }
  
      &:last-child {
        width: 100%;
        margin-top: 7px;
      }
    }

    // order page 
    &_order {

      .form__wrap-select {
        margin-right: 0;
      }
    }

    &__block_order {
      .input, .form__wrap-select {
        width: 100%;
        min-width: auto;
      }
    }
    // order page end

    // login page 
    &_login {
      width: 100%;
    }
  
    &__block_login {
      margin-bottom: 20px;
    }
    // login page end


    // personal page 4 Edit
    &_edit {
      .form__attach {
        padding-left: 25px;
      }
    }
  
    &__grid-el_edit-wide {
      display: block;
      .form-group {
        width: 100%;
      }
      .form__link {
        margin-top: 7px;
        padding-left: 0;
        width: 100%;
        text-align: right;
      }
    }
    // personal page 4 Edit end


    // personal page 3 Edit
    &__grid_data {
      display: flex;

      .form__grid-el {
        &:first-child {
          width: 50%;
        }
    
        &:last-child {
          width: 50%;
        }
      }
    }
    // personal page 3 Edit end
  }
}