.subscribe {
  margin-top: 60px;
  margin-bottom: 120px;
  color: $g-col-l-gray;
  &__content {
    display: flex;
    justify-content: center;
    padding-top: 60px;
    padding-bottom: 70px;
    border-radius: 10px;
    background-color: $g-col-blue;
  }

  &__wrap {
    max-width: 83%;
    width: 100%;
    margin-left: -30px;
    display: flex;
    align-items: center;
  }

  &__text {
    margin-bottom: 0;
    @include font-size('p1');
    line-height: 1.5;
    width: 40%;
    padding-left: 30px;
    letter-spacing: 0.01em;
  }

  &__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 60%;
    padding-left: 30px;
  }

  &__wrap-input {
    margin-right: 30px;
    max-width: 300px;
    width: 100%;

  }

  &__input {
    height: 45px;
    @include font-size('p2');
    color: $g-col-l-gray;

    font-style: italic;
    padding-left: 0;
    padding-right: 10px;
    background-color: transparent;
    border: 0;
    border-radius: 0;
    border-bottom: 1px solid $g-col-l-gray;

    &::placeholder {
      color: $g-col-l-gray;
    }
  }

  .btn {
    max-width: 220px;
    width: 100%;
    color: $g-col-blue;

    &:hover {
      color: $g-col-white;
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .subscribe {

    &__content {
      padding-left: 20px;
      padding-right: 20px;
    }
    &__wrap {
      margin-left: 0;
      max-width: 100%;
    }

    &__text {
      padding-left: 0;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .subscribe {
    margin-bottom: 70px;
    &__content {
      padding-top: 25px;
      padding-bottom: 20px;
    }

    &__wrap {
      display: block;
    }

    &__text {
      margin-bottom: 20px;
      width: 100%;
    }
    &__form {
      display: block;
      width: 100%;
      padding-left: 0;
    }

    &__wrap-input {
      margin-bottom: 45px;
    }

    .btn {
      max-width: 100%;
    }
  }
}

.contacts {
  margin-top: 120px;
  margin-bottom: 70px;
  display: flex;
  color: $g-col-blue;
  background-color: $g-col-white;
  border-radius: 10px;
  overflow: hidden;
  &__info {
    width: 40%;
    margin-right: 30px;
    padding-top: 30px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
  }

  &__title {
    @include font-size('h2');
    font-weight: 700;
  }

  &__wrap {
    margin-bottom: 20px;
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;

    &_time {
      margin-bottom: 40px;
    }
    p {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &__phone {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: -40px;
    margin-bottom: 40px;
    @include font-size('p13');

    a {
      padding-left: 40px;
      margin-bottom: 15px;
    }

    span {
      margin-left: 10px;
    }
  }

  &__map {
    flex-grow: 1;
  }

  .btn {
    margin-bottom: 40px;
    max-width: 300px;
    width: 100%;
    color: $g-col-white;

    &:hover {
      color: $g-col-green;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .contacts {
    flex-direction: column-reverse;
    &__info {
      width: 100%;
      padding-bottom: 60px;
    }

    &__wrap {
      margin-bottom: 15px;

      &_time {
        margin-bottom: 20px;
      }
    }

    &__phone {
      margin-bottom: 15px;
    }

    .btn {
      margin-bottom: 30px;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .contacts {
    margin-bottom: 50px;
    .social {
      .icon {
        width: 44px;
        height: 44px;
      }
    }

    &__info {
      padding-left: 20px;
      padding-right: 20px;
    }

    &__phone {
      margin-left: 0;

      a {
        padding-left: 0;
      }
    }

    .btn {
      max-width: 100%;
    }

    &__map {
      height: 350px;
    }
  }
}

.f-menu {
  margin-top: 60px;
  padding-top: 60px;
  padding-bottom: 50px;
  color: $g-col-white;
  background: $g-col-blue;

  &__title {
    margin-bottom: 10px;
    @include font-size('h3');
    font-weight: 700;
  }

  &__list {
    padding-right: 20px;
    @include font-size('p5');
    font-family: $g-font-extra;
  }

  &__link {
    display: block;
    padding-top: 7px;
    padding-bottom: 7px;

    &:hover {
      opacity: 0.7;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .f-menu {
    margin-top: 50px;
    padding-top: 25px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.copyright {
  padding-top: 15px;
  padding-bottom: 25px;
  color: $g-col-blue;
  background-color: $g-col-white;

  &__wrap {
    display: flex;
    justify-content: space-between;
  }

  &__text {
    margin: 0;
    @include font-size('p6');
    text-decoration: underline;
    font-family: $g-font-extra;
  }
}

@media (max-width: $breakpoint-small) {
  .copyright {
    &__wrap {
      display: block;
    }

    &__text:first-child {
      margin-bottom: 15px;
    }
  }
}