.search {

  &__form {
    position: relative;

  }
  
  .input {
    padding-left: 20px;
    padding-right: 40px;
  }

  &__btn {
    position: absolute;
    padding: 0;
    width: 30px;
    height: 30px;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;

    &:hover {
      opacity: 0.7;
    }
    
    &_left {
      left: 15px;
    }

    &_right {
      right: 15px;
    }
  }
  
  // search page
  &_page {
    margin-top: 30px;
    margin-bottom: 60px;

    .btn {
      max-width: 280px;
      width: 100%;
      color: $g-col-white;

      &:hover {
        background-color: $g-col-green-hov;
      }
    }
  }

  &__form_page {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .form-group {
      position: relative;
      flex-grow: 1;
      margin-right: 30px;
      .icon {
        position: absolute;
        top: 50%;
        left: 15px;
        transform: translateY(-50%);
      }

      .input {
        padding-left: 60px;
        height: 52px;
        @include fluidFontSize(20px);

        &::placeholder {
          color: $g-col-gray-strouk;
        }
      }
    }
  }
  // search page end
}

@media (max-width: $breakpoint-medium) {

  .search {

    // search page

    &_page {
  
      .btn {
        margin-top: 15px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    &__form_page {
      display: block;

      .form-group {
        margin-right: 0;

        .input {
          padding-left: 50px;
        }
      }
    }
    // search page end
  }
}

@media (max-width: $breakpoint-medium) {

  .search {

    // search page
    &_page {
      margin-top: 15px;
      margin-bottom: 30px;
      padding-left: 10px;
      padding-right: 10px;
      .btn {
        max-width: 100%;
      }
    }
    // search page end
  }
}