.bill-pay {
  margin-top: 60px;
  margin-bottom: 90px;
  padding-top: 30px;
  padding-bottom: 35px;
  background-color: #fff;

  &__content {
    position: relative;
    align-items: center;
    &:before {
      position: absolute;
      content: "";
      top: 0;
      left: 50%;
      width: 1px;
      height: 100%;
      background: $g-col-gray-strouk;
    }
  }

  &__list {
    @include fluidFontSize(18px);
    margin-top: -15px;
    letter-spacing: 0.01em;
    color: $g-col-blue;
    column-count: 3;
  }

  &__link {
    display: inline-block;
    margin: 5px 0;
    padding: 10px 0;

    &:hover {
      color: $g-col-green;
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__title {
    margin-bottom: 15px;
    color: $g-col-blue;
    @include font-size('h3');
    font-weight: 700;
  }

  &__text {
    margin: 0;
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;
  }
}

@media (max-width: $breakpoint-medium) {
  .bill-pay {
    &__list {
      column-count: 2;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .bill-pay {
    margin-top: 20px;
    margin-bottom: 50px;
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
    &__content {
      &:before {
        content: none;
      }
    }
  }
}