.m-rate {
  margin-bottom: 10px;

  &__content {
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 60px;
    border-radius: 10px;
    background-color: #fff;
  }

  &__table {
    display: flex;
  }

  &__th {
    @include fluidFontSize(12px, 18px);
    letter-spacing: 0.01em;
  }

  &__td {
    color: $g-col-blue;
  }

  &__td_flag {
    @include fluidFontSize(16px);
    text-transform: uppercase;

    p {
      margin: 0;
    }
  }

  &__td_flag {
    &:first-child {
      width: 12%;
    }

    &:nth-child(2) {
      width: 40%;
    }
  }

  &__rows_desk {
    .m-rate__td_num {
      width: 22%;
    }
  }

  &__flag {
    margin-bottom: 10px;

    img {
      width: 34px;
      height: 34px;
      overflow: hidden;
      border-radius: 50%;
    }
  }

  &__wrap-flag {
    margin-right: 40px;
  }

  &__td_num {
    line-height: 1;
    @include fluidFontSize(14px, 26px);
    font-weight: 500;
  }

  &__row {
    display: flex;
    align-items: center;
  }
}