.social {
  &_small {   
    .icon {
      width: 30px;
      height: 30px;
    }

    .social__list {
      margin-left: -10px;
    }

    .social__item {
      margin-left: 10px;
      margin-bottom: 10px;
    }
  }

  &_big {   
    .icon {
      width: 48px;
      height: 48px;
    }

    .social__list {
      margin-left: -20px;
    }

    .social__item {
      margin-left: 20px;
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    opacity: 0.2;
    transition: $g-anime;


    &:hover {
      opacity: 0.5;
    }
  }

}