//--------------------------------
// Global breakpoints
//--------------------------------
$breakpoint-small:                              640px !default;  // Phone landscape
$breakpoint-medium:                             960px !default;  // Tablet Landscape
$breakpoint-large:                              1200px !default; // Desktop
$breakpoint-xlarge:                             1600px !default; // Large Screens

$breakpoint-default:                             1366px !default; // default width

// 1. Your custom variables and variable overwrites.
@mixin fluidFontSize($f-min, $f-def: $f-min, $w-def:$breakpoint-default, $w-min: $breakpoint-medium, $w-max: $breakpoint-default) {

	$f-new-def: $f-def - ($w-def / 200);
	font-size: calc(#{$f-new-def} + 0.5vw);

	@media (max-width: $w-min) {
		font-size: $f-min;
	}
	@media (min-width: $w-max) {
		font-size: $f-def;
	}
}

$g-font: 'Roboto', sans-serif;
$g-font-extra: 'Open Sans', sans-serif; 

$g-col-green: #A3C156;
$g-col-l-green: #E7ECDA;
$g-col-green-hov: #86B946;

$g-col-text: #7D7D7D;
$g-col-l-gray: #F5F5F5;
$g-col-gray-strouk: #C4C4C4;

$g-col-blue: #11133F;
$g-col-black: #000;
$g-col-white: #fff;

$g-col-red: #EB5757;
$g-col-red-hov: #CC2424;
$g-anime: 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);


$global-font-family: $g-font;

$global-color: $g-col-text;
$dropdown-color: $g-col-blue;

$base-heading-color: $g-col-blue;

$breadcrumb-item-font-size: 13px;
$breadcrumb-item-color: $g-col-gray-strouk;
$breadcrumb-item-hover-color: $g-col-blue;
$breadcrumb-item-active-color: $g-col-gray-strouk;
$breadcrumb-divider-margin-horizontal: 5px;
$breadcrumb-divider-color: $g-col-gray-strouk;

$global-link-color: $g-col-blue;
$global-link-hover-color: $g-col-blue;

$pagination-item-color: $g-col-text;
$pagination-item-hover-color: $g-col-blue;
$pagination-item-active-color: $g-col-white;
$pagination-margin-horizontal: 10px;

// 2. Import default variables and available mixins.
@import "../uikit-lib/src/scss/variables-theme.scss";
@import "../uikit-lib/src/scss/mixins-theme.scss";

// 3. Your custom mixin overwrites.
// @mixin hook-card() { color: #000; }
@mixin hook-pagination-item() {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 29px;
	height: 29px;
	@include fluidFontSize(16px);
	font-weight: 700;
	border: 1px solid transparent;
	border-radius: 50%;
	line-height: 1;
	transition: $g-anime;
}

@mixin hook-pagination-item-active() {
  background: $g-col-blue;
}

@mixin hook-pagination-item-hover() {
	border-color: $g-col-blue;
}



// 4. Import UIkit.
@import "../uikit-lib/src/scss/uikit-theme.scss";
