.card-page {
  margin-top: 50px;
  margin-bottom: 90px;
  padding-top: 50px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 60px;
  background-color: $g-col-white;
  border-radius: 10px;
}

.card-info {
  margin-bottom: 20px;
  .uk-grid + .uk-grid {
    margin-top: 80px;
  }
  &__img {
    padding-right: 15%;
    img {
      width: 100%;
      object-fit: contain;
      border-radius: 10px;
    }
  }

  &__title {
    @include fluidFontSize(24px, 26px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__text {
    @include font-size('p1');
    line-height: 1.5;
    letter-spacing: 0.01em;
  }

  &__list {
    padding-right: 15%;
    column-count: 2;
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;
  }

  &__item {
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 40px;

    &:before {
      position: absolute;
      content: "";
      top: 50%;
      left: 10px;
      transform: translateY(-50%);
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background-color: $g-col-blue;
    }
  }

  &__features {
    display: flex;
    margin-top: 40px;
    margin-bottom: 60px;
  }

  &__el {
    margin-right: 40px;
  }

  &__subtitle {
    margin-bottom: 10px;
    @include fluidFontSize(18px, 36px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__subtext {
    margin: 0;
    @include fluidFontSize(16px, 13px);
  }

  .btn {
    max-width: 240px;
    width: 100%;
    margin-right: 80px;
    color: $g-col-white;

    &:hover {
      background-color: $g-col-green-hov;
    }
  }

  &__btn-wrap {
    display: flex;
    align-items: center;
  }

  .link {
    @include fluidFontSize(18px);

    span {
      margin-right: 10px;
      text-decoration: underline;
    }
    &:hover .icon {
      fill: $g-col-green;
      stroke: $g-col-green;
    }
  }

  .icon {
    fill: $g-col-blue;
    stroke: $g-col-blue;
    transition: $g-anime;
  }
}

.card-form {
  
  &__title {
    margin-bottom: 30px;
    @include fluidFontSize(24px, 26px);
    font-weight: 700;
    color: $g-col-blue;
  }
}

@media (max-width: $breakpoint-large) {
  .card-info {

    &__img {
      padding-right: 5%;
    }

    &__list {
      padding-right: 5%;
    }

    .btn {
      margin-right: 40px;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .card-info {

    &__img {
      max-width: 600px;
      padding-right: 0;
    }

    &__list {
      max-width: 600px;
      padding-right: 0;
    }

    .btn {
      margin-right: 40px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .card-page {
    margin-top: 15px;
    margin-bottom: 80px;
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .card-info {
    margin-bottom: 50px;
    .uk-grid + .uk-grid {
      margin-top: 20px;
    }
    &__features {
      margin-top: 30px;
      margin-bottom: 20px;
      display: block;
    }

    &__el {
      display: flex;
      margin-right: 0;
      margin-bottom: 15px;
    }

    &__subtitle {
      width: 25%;
    }

    &__subtext {
      width: 75%;
    }

    &__text-wrap {
      order: -1;

      p {
        margin: 0;
      }
    }

    &__list {
      margin-bottom: 30px;
      column-count: unset;
    }

    &__item {
      padding-left: 30px;

      &::before {
        left: 0;
        width: 8px;
        height: 8px;
      }
    }

    &__btn-wrap {
      display: block;
    }

    .btn {
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
}