.cards {
  margin-top: 60px;
  margin-bottom: 90px;

  &__el {
    padding-top: 30px;
    padding-bottom: 60px;
    transition: $g-anime;
    border-radius: 10px;
    background-color: $g-col-white;

    &:not(:first-child) {
      margin-top: 30px;
    }

    &:hover {
      box-shadow: $g-shadow;
      .cards__title {
        color: $g-col-green;
      }

      .icon {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__content {
    margin-left: -30px;
    display: flex;
  }


  &__img {
    width: unquote('calc(100% * 5 / 12.001)');
    padding-left: 90px;
    img {
      width: 100%;
      object-fit: contain;
      border-radius: 10px;
    }
  }

  &__wrap {
    width: unquote('calc(100% * 7 / 12.001)');
    padding-left: 30px;
    padding-right: 60px;
  }

  &__title {
    @include fluidFontSize(24px, 26px);
    font-weight: 700;
    color: $g-col-blue;
    transition: $g-anime;
  }

  &__list {
    margin-bottom: 30px;
    column-count: 2;
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;
  }

  &__item {
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 40px;

    &:before {
      position: absolute;
      content: "";
      top: 50%;
      left: 10px;
      transform: translateY(-50%);
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background-color: $g-col-blue;
    }
  }

  .icon {
    visibility: hidden;
    opacity: 0;
    transition: $g-anime;
  }

  .btn {
    max-width: 320px;
    color: $g-col-white;
    &:hover {
      border-color: $g-col-green;
      background-color: $g-col-green;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .cards {

    &__img {
      width: 50%;
      padding-left: 50px;
    }

    &__wrap {
      width: 50%;
      padding-right: 20px;
    }

    &__list {
      column-count: unset;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .cards {
    margin-top: 15px;
    margin-bottom: 50px;

    &__el {
      padding-left: 20px;
      padding-right: 20px;
      &:not(:first-child) {
        margin-top: 10px;
      }
    }

    &__content {
      display: block;
      margin-left: 0;
    }

    &__item {
      padding-left: 30px;
      &::before {
        left: 0;
        width: 8px;
        height: 8px;
      }
    }

    &__img {
      width: 100%;
      margin-bottom: 30px;
      padding-left: 0;
    }

    &__wrap {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
    }

    .btn {
      max-width: 100%;
    }
  }
}