.m-slider {
  margin-top: 20px;
  margin-bottom: 60px;

  &__list {
    border-radius: 10px;
    overflow: hidden;
  }

  &__item {
    padding-left: 8.33%;
    background-color: $g-col-blue;
    color: #fff;
  }

  &__wrap {
    width: 33.33%;
    padding-top: 60px;
    padding-bottom: 40px;
  }

  &__img {
    position: absolute;
    max-width: 50%;
    right: 8.33%;
    bottom: 0;
  }

  &__subtitle {
    @include font-size('p5');
    opacity: 0.5;
    text-transform: uppercase;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 50px;
    @include font-size('h1');
    letter-spacing: 0.3px;
    font-weight: 900;
  }

  &__text {
    margin-top: 0;
    margin-bottom: 50px;
    @include fluidFontSize(12px, 16px);
    line-height: 1.9;
  }

  &__btn-wrap {
    display: flex;
    align-items: center;
  }

  .btn {
    width: 220px;
  }

  .link {
    width: 160px;
    margin-left: 20px;
    padding-right: 0;
  }

  &__nav {
    position: absolute;
    top: 60px;
    right: 40px;
  }

  .pointer_left {
    margin-right: 30px;
  }

  &__dotnav {
    position: absolute;
    right: 40px;
    bottom: 30px;
    padding-left: 10px;
    counter-reset: dot-num;
    color: $g-col-l-gray;
    @include fluidFontSize(10px, 18px);
    opacity: 0.5;
    font-weight: 500;

    a {
      display: none;
    }

    &::after {
      content: "/"counter(dot-num);
    }

    li {
      position: absolute;
      left: 0;
      padding-left: 0;
      &:before {
        visibility: hidden;
        opacity: 0;
        counter-increment: dot-num;
        content: counter(dot-num);
        transition: $g-anime;
      }

      &.uk-active {
        &:before {
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .m-slider {
    &__img {
      display: none;
    }

    &__wrap {
      width: 100%;
      height: 100%;
      padding-top: 30px;
      padding-bottom: 25px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
  }
}


@media (max-width: $breakpoint-small) {

  .m-slider {
    margin-top: 15px;
    margin-bottom: 50px;
    &__item {
      padding-left: 20px;
      padding-right: 20px;
    }

    &__btn-wrap {
      flex-wrap: wrap;
    }

    &__wrap {
      justify-content: space-between;
    }

    &__subtitle {
      margin-bottom: 35px;
    }

    &__title {
      margin-bottom: 30px;
    }

    &__text {
      margin-bottom: 30px;
    }

    .btn {
      width: 100%;
      margin-bottom: 30px;
    }

    .link {
      margin-left: 0;
      width: 70px;
      text-align-last: left;
    }

    &__nav {
      top: 25px;
      right: 20px;
    }

    &__dotnav {
      padding-left: 7px;
      right: 20px;
      bottom: 25px;
    }
  }
}


//--------------------------------
// here --> intro
//--------------------------------
.intro {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  margin-bottom: 60px;
  padding-top: 65px;
  padding-bottom: 65px;
  padding-left: 8.33%;
  padding-right: 16.66%;
  border-radius: 10px;
  background: #fff;
  &__img {
    margin-right: 60px;
  }

  &__title {
    @include fluidFontSize(18px, 36px);
    font-weight: 700;
    color: $g-col-blue;
  }

  &__text {
    @include font-size('p1');
    letter-spacing: 0.01em;
  }
}

@media (max-width: $breakpoint-large) {

  .intro {
    padding-right: 8.33%;
  }
}

@media (max-width: $breakpoint-small) {
  .intro {
    position: relative;
    display: block;
    margin-top: 50px;
    padding-left: 20px;
    padding-right: 40px;
    padding-top: 30px;
    padding-bottom: 40px;

    &__img {
      position: absolute;
      top: 30px;
      left: 20px;
      width: 40px;
      height: 40px;
    }
  
    &__title {
      padding-left: 60px;
    }

    &__text {
      line-height: 21px;
    }
  }

}


//--------------------------------
// here --> block
//--------------------------------
.block {
  margin-top: 120px;
  margin-bottom: 60px;

  &__wrap {
    display: flex;
    align-items: baseline;
    margin-bottom: 20px;
    color: $g-col-blue;
  }

  &__title {
    margin-right: 110px;
    @include font-size('h2');
  }
}

@media (max-width: $breakpoint-small) {
  .block {
    margin-top: 60px;
    margin-bottom: 50px;
    &__wrap {
      margin-bottom: 30px;
      padding-left: 10px;
      padding-right: 10px;
      justify-content: space-between;
      align-items: flex-end;
    }
    &__title {
      margin-right: 20px;
      margin-bottom: 0;
    }

    .link {
      min-width: 100px;
      margin-bottom: 5px;
    }
  }
}


//--------------------------------
// here --> ad-unit
//--------------------------------
.ad-unit {
  display: flex;
  margin-top: 60px;
  margin-bottom: 60px;
  overflow: hidden;
  background-color: $g-col-green;
  border-radius: 10px;

  color: #fff;
  &__wrap {
    width: 50%;
    padding-top: 30px;
    padding-left: 40px;
    padding-bottom: 40px;
  }

  &__img {
    padding-left: 15px;
    display: flex;
    width: 50%;

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  &__title {
    @include font-size('h3');
    color: inherit;
  }

  &__text {
    margin-bottom: 40px;
    @include font-size('p1');
  }

  .btn {
    max-width: 240px;
  }
}

@media (max-width: $breakpoint-medium) {
  .ad-unit {
    flex-direction: column;

    &__img {
      padding-left: 0;
      width: 100%;
      order: -1;
    }

    &__wrap {
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 60px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .ad-unit {
    .btn {
      max-width: 100%;
    }

    &__text {
      max-width: 200px;
      margin-bottom: 30px;
    }
  }
}

//--------------------------------
// here --> currency
//--------------------------------
.currency {
  display: flex;
  &__calc {
    width: 57.3%;
    padding-top: 34px;
    padding-left: 30px;
    padding-right: 35px;
    padding-bottom: 20px;
    background-color: #fff;
    border-radius: 10px;
    color: $g-col-blue;
  }

  &__exchange {
    margin-left: 30px;
    flex-grow: 1;
    padding-top: 34px;
    padding-left: 30px;
    padding-right: 35px;
    padding-bottom: 20px;
    background-color: $g-col-blue;
    border-radius: 10px;

    color: #fff;
  }
  &__title {
    margin-bottom: 10px;
    font-weight: 700;
    @include font-size('h3');
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    @include font-size('p1');
    margin-bottom: 30px;
    letter-spacing: 0.01em;
    p {
      margin: 0;
    }
  }

  // currency page 
  &_page {
    margin-top: 60px;
    margin-bottom: 90px;

    .icon {
      fill: $g-col-white;
    }
    .btn_green {
      max-width: 240px;

      &:hover {
        color: $g-col-white;
        background-color: $g-col-green-hov;
      }
    }
  }

  &__calc_page {
    width: 100%;
    padding-bottom: 40px;
    color: $g-col-white;
    background-color: $g-col-blue;
  }
  //currency page end
}

.calc {

  &__table {
    display: flex;
    flex-wrap: wrap;
  }

  &__col {
    &:first-child {
      width: 20%;
    }

    &:nth-child(2) {
      width: 46%;
    }

    &:nth-last-of-type(-n+2) {
      width: 17%;

      .calc__td:nth-child(2) {
        position: relative;

        &:before {
          position: absolute;
          content: "";
          width: 100%;
          height: 1px;
          top: 50%;
          left: 0;
          background: $g-col-l-gray;
        }
      }
    }
  }

  &__td {
    display: flex;
    align-items: center;
  }

  &__th {
    margin-bottom: 10px;
    padding: 15px 0;
    border-bottom: 1px solid $g-col-l-gray;
    @include font-size('p3');
    font-family: $g-font-extra;
  }

  &__td, input, select {
    color: $g-col-blue;
    @include font-size('h3');
    font-weight: 700;
    text-transform: uppercase;
    height: 50px;
  }

  &__th, &__td {
    padding-right: 10px;
  }

  &__select {
    position: relative;
    width: 100%;
    &:after {
      content: "";
      position: absolute;
      right: 12px;
      top: 50%;
      z-index: 0;
      transform: translateY(-50%);
      display: inline-block;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 8px solid $g-col-text;
    }
  }

  select {
    position: relative;
    z-index: 1;
    width: 100%;
    padding-left: 13px;
    background-color: transparent;
    border-color: $g-col-green;
    border-radius: 5px;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  input {
    max-width: 260px;
    background-color: transparent;
    padding-left: 20px;
  }

  .btn {
    margin-left: 50px;
  }

  // calc page
  &_page {
    margin-bottom: 40px;
    .calc__td, input, select {
      color: $g-col-white;
    }
    .calc__th {
      margin-bottom: 20px;
    }

    input {
      max-width: 430px;
      margin-right: 30px;
    }
    option {
      color: $g-col-blue;
    }
  }
  
  //calc page end
}

.exchange {

  &__table {
    margin-bottom: 25px;
    width: 100%;
    text-align: left;
  }

  &__thead {
    @include font-size('p3');
    font-family: $g-font-extra;
  }

  &__tbody {
    @include font-size('h3');
    font-weight: 700;
    text-transform: uppercase;
  }

  &__th, &__td {
    display: inline-block;
    padding-right: 10px;
    &:first-child {
      width: 45%;
    }

    &:nth-child(2) {
      width: 25%;
    }
    &:last-child {
      width: 30%;
    }
  }

  &__td {
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid rgba($g-col-text, 0.4);
  }

  &__tr:last-child {
    .exchange__td {
      border: none;
    }
  }
}

@media (max-width: $breakpoint-large) {

  .calc {

    &__col {
      &:first-child {
        width: 25%;
      }
  
      &:nth-child(2) {
        width: 35%;
      }
  
      &:nth-last-of-type(-n+2) {
        width: 20%;
      }
    }
  }


  .exchange {
    &__th, &__td {

      &:first-child {
        width: 40%;
      }
  
      &:nth-child(2) {
        width: 30%;
      }
      &:last-child {
        width: 30%;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .currency {
    display: block;

    &__calc {
      width: 100%;
      margin-bottom: 60px;
    }

    &__exchange {
      margin-left: 0;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .currency {

    &__wrap {
      display: block;
    }

    &__exchange {
      padding-top: 15px;
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 50px;
      .currency__wrap {
        .currency__text {
          margin-bottom: 30px;
        }
      }
    }

    &__calc {
      padding-top: 20px;
      padding-left: 10px;
      padding-right: 10px;
    }

    //currency page
    &_page {
      margin-top: 15px;
      margin-bottom: 60px;
      .btn_green {
        max-width: 100%;
      }
    }

    &__calc_page {
      margin-bottom: 0;
      padding-bottom: 60px;
    }
    //currency page end
  }

  .calc {
    &__table {
      display: block;
    }
    &__col {
      &:nth-child(n + 1) {
        width: 100%;
      }

      &:nth-last-of-type(-n+2) {
        .calc__td {
          padding-left: 10px;

        }
        .calc__td:nth-child(2):before {
          content: none;
        }
      }
    }

    &__wrap {
      display: flex;
      margin-bottom: 15px;
    }

    &__td {
      width: 100%;
      padding-right: 0;

      &:nth-child(2) {
        justify-content: center;
        max-width: 45px;
      }
    }

    &__th {
      margin-bottom: 20px;
    }

    .btn {
      margin-left: 0;
    }

    input {
      padding-left: 10px;
    }

    // calc page
    &_page {
      margin-bottom: 30px;
      input {
        margin-right: 0;
      }
    }
    // calc page end

  }
}

//--------------------------------
// here --> about
//--------------------------------

.about {
  display: flex;
  padding-top: 30px;

  &__wrap {
    width: 40%;
    margin-right: 30px;

    & > div {
      max-width: 410px;
    }
  }

  &__title {
    color: $g-col-blue;
    @include font-size('h3');
    font-weight: 700;
  }

  &__text {
    @include font-size('p1');
    line-height: 1.5;
    letter-spacing: 0.01em;
  }

  &__work-done {
    flex-grow: 1;

    & > div {
      max-width: 640px;
    }
  }
}

.work-done {
  color: $g-col-blue;
  &__section {
    display: flex;

    &:first-child {
      margin-bottom: 30px;
      padding-bottom: 15px;
      align-items: center;
      border-bottom: 1px solid $g-col-blue;
    }

    &:last-child {
      justify-content: space-between;
    }
  }

  &__b-num {
    min-width: 280px;
    margin: 0;
    @include font-size('h4');
    font-weight: 900;
    line-height: 1;
  }

  &__title {
    max-width: 290px;
    margin: 0;
    margin-left: 10px;
    @include font-size('h5');
    font-weight: 500;
  }

  &__num {
    margin-bottom: 10px;
    @include font-size('p11');
    font-weight: 700;
  }

  &__wrap {
    max-width: 200px;
    padding-right: 20px;
  }

  &__text {
    margin-top: 10px;
    @include fluidFontSize(18px);
    letter-spacing: 0.01em;
  }
}

@media (max-width: $breakpoint-medium) {
  .about {
    display: block;

    &__wrap {
      margin-bottom: 90px;
      width: 100%;
      & > div {
        max-width: 100%;
      }
    }
    &__work-done {

      & > div {
        max-width: 100%;
      }
    }
  }

  .work-done {
    &__title {
      max-width: 100%;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .about {

    &__wrap {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  .work-done {
    &__section {
      display: block;
    }

    &__b-num {
      min-width: auto;
      padding: 0 10px;
      margin-bottom: 20px;
    }

    &__title {
      margin-left: 0;
      padding: 0 10px;
    }

    &__wrap {
      display: flex;
      justify-content: space-between;
      max-width: 100%;
      padding-left: 10px;
      padding-right: 10px;
    }

    &__num {
      width: 30%;
      text-align: right;
    }

    &__text {
      width: 60%;
    }
  }
}

