.p-news {
  margin-top: -30px;
  margin-bottom: 60px;
  &__wrap-date {
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__date {
    @include font-size('p3');
    font-family: $g-font-extra;
    color: $g-col-green;
  }

  &__img {
    margin-bottom: 60px;
    border-radius: 5px;
    overflow: hidden;
  }

  &__text {
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;
  }

  &__link {
    letter-spacing: 0.01em;
    @include font-size('p1');
    text-decoration: underline;
    color: $g-col-blue;

    &:hover {
      text-decoration: none;
    }
  }

  &__wrap-title {
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    color: $g-col-blue;
  }

  &__title {
    @include fluidFontSize(26px, 36px);
    font-weight: 700;
    margin: 0;
  }

  .link {
    font-size: 12px;
  }
}

@media (max-width: $breakpoint-small) {
  .p-news {
    margin-top: 10px;
    margin-bottom: 50px;
    padding-left: 20px;
    padding-right: 20px;

    &__wrap-date {
      margin-bottom: 20px;
    }

    &__img {
      margin-bottom: 30px;
    }

    &__content {
      margin-bottom: 30px;
    }
  }

}