.service {
  display: flex;
  flex-wrap: wrap;
  margin-left: -30px;

  &__item {
    display: flex;
    min-height: 215px;
    width: 25%;
    margin-bottom: 30px;
    padding-left: 30px;

    
    &:first-child {
      width: 50%;

      .service__title {
        max-width: 410px;
      }
      .service__text {
        max-width: 410px;
      }
    }
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding-top: 30px;
    padding-left: 20px;
    padding-bottom: 40px;
    padding-right: 40px;
    background-color: #fff;
    border-radius: 10px;

    overflow: hidden;

    &:hover {
      box-shadow: $g-shadow;

      .service__hov {
        width: 35px;

        .pointer {
          transform: translate(-50%, -50%);
        }
      }
    }
  }

  &__hov {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 100%;
    background: $g-col-green;
    transition: $g-anime;
    overflow: hidden;
  }

  .pointer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-200%, -50%);
    transition: $g-anime;
    transition-delay: 0.3s;
  }

  &__title {
    margin-bottom: 30px;
    color: $g-col-blue;
    @include font-size('h3');
    font-weight: 700;
    line-height: 1;
  }

  &__text {
    margin: 0;
    @include fluidFontSize(12px, 16px);
    line-height: 1.3;
  }
}

@media (max-width: $breakpoint-medium) {
  .service {
    &__item {
      width: 50%;
      min-height: 80px;

      &:first-child {
        width: 100%;
      }
    }
  }
}


@media (max-width: $breakpoint-small) {
  .service {
    margin-left: -10px;
    &__item {
      padding-left: 10px;
      margin-bottom: 10px;

      &:not(:first-child) {
        .service__text {
          display: none;
        }

        .service__wrap {
          padding-right: 20px;
          padding-bottom: 20px;
        }

        .service__title {
          margin-bottom: 0;
        }

        .service__hov {
          display: none;
        }
      }

      &:first-child {
        .service__wrap {
          padding-right: 65px;
        }

        .service__hov {
          width: 45px;

          .pointer {
            transform: translate(-50%, -50%);
          }
        }
      }
    }

    &__wrap {
      padding-top: 15px;
    }
  }
}