.card-filter {
  margin-top: 30px;
  margin-bottom: 60px;

  &__list {
    display: flex;

    li {
      margin-right: 60px;
    }
  }



  .link {
    padding-top: 5px;
    padding-bottom: 5px;

    &.active {
      pointer-events: none;
      cursor: default;
      color: $g-col-green;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .card-filter {
    margin-top: 15px;
    margin-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;

    &__list {
      display: flex;
      justify-content: space-between;
      li {
        margin-right: 0;
      }
    }
  }
}