.pers-pg {
  margin-top: 25px;
  margin-bottom: 90px;

  &_fst {
    margin-bottom: 50px;
  }

  &__link {
    margin-top: 45px;
    padding-left: 30px;
    text-align: left;
    @include fluidFontSize(18px);
  }

  &__wrap-links {
    margin-top: 15px;
    display: flex;
    justify-content: center;

    a:nth-child(2) {
      margin-left: 30px;
    }
  }
}

.wrapper-fm {
  margin-bottom: 30px;
  padding-top: 35px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 45px;
  border-radius: 5px;
  background-color: $g-col-white;

  &_tabs {
    padding: 0;
    background-color: transparent;
    border-radius: unset;
  }

  &__title {
    color: $g-col-blue;
    @include fluidFontSize(18px, 26px);
    font-weight: 700;
  }

  &__subtext {
    margin-top: 45px;
    margin-bottom: 60px;
    color: $g-col-blue;
    letter-spacing: 0.01em;
    text-align: center;
  }

  &__text-s {
    margin: 0;
    @include fluidFontSize(18px);
  }

  &__text-b {
    margin: 0;
    @include fluidFontSize(24px, 26px);
    font-weight: 700;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $g-col-blue;
    border-radius: 5px;
    color: $g-col-blue;
    height: 50px;
    transition: $g-anime;

    &:hover {
      border-color: $g-col-green-hov;
      color: $g-col-green-hov;
    }
  }

  &__title_sum {
    margin: 0;
    pointer-events: none;
    &:before, &:after {
      content: none;
    }

    &:hover, &:focus {
      color: $g-col-blue;
    }
  }

  &__wrap-title {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    .wrapper-fm__title {
      margin: 0;
    }
  }

  &_card-data {
    max-width: 640px;
  }
}

.tabs-content {
  padding: 60px 30px;
  background-color: $g-col-white;
  border-radius: 5px;
}

@media (max-width: $breakpoint-large) {

  .pers-pg {
    &__link {
      margin-top: 15px;
    }
  }
}

@media (max-width: $breakpoint-medium) {

  .tabs-content {
    padding: 0;
    background-color: transparent;
  }

  .wrapper-fm {
    &_tabs {
      padding-top: 20px;
      padding-bottom: 50px;
      padding-left: 30px;
      padding-right: 30px;
      background-color: $g-col-white;
      border-radius: 5px;
    }

    &__title_sum {
      width: 100%;
      margin: 0;
      pointer-events: initial;
      cursor: pointer;
      &:after {
        content: "";
      }
    }

    &_sum {
      padding-bottom: 20px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .pers-pg {
    margin-top: 15px;
    margin-bottom: 40px;

    .uk-grid-margin {
      margin-top: 10px;
    }

    &__link {
      padding-left: 0;
      text-align: center;
    }
  }
  .wrapper-fm {
    margin-bottom: 10px;
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;

    &__subtext {
      margin-top: 25px;
      margin-bottom: 40px;
    }

    &__wrap-title {
      display: block;
      margin-bottom: 15px;

      .link {
        text-align: left;
        margin-top: 15px;
      }
    }
  }
}