.summary {

  overflow-x: auto;

  &::-webkit-scrollbar {
    height: 2px;
  }
  &::-webkit-scrollbar-track {
    background: $g-col-gray-strouk;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $g-col-blue;
    border-radius: 5px;
  }

  
  &__table {
    width: 100%;
  }

  &__tr {
    display: flex;
    align-items: center;
  }

  &__th {
    font-weight: 400;
    @include fluidFontSize(12px, 13px);
  }

  &__td {
    @include font-size('p1');
    color: $g-col-blue;
    letter-spacing: 0.01em;
  }

  &__thead {
    .summary__tr {
      padding-bottom: 20px;
      margin-bottom: 5px;
      border-bottom: 1px solid #e5e5e5;
    }
  }

  &__tbody {
    .summary__tr {
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  &__th, &__td {
    padding-right: 10px;
    text-align: left;
  }

  &__td_links {
    display: flex;
    align-items: center;
  }

  &_col-3 {
    .summary__table {
      min-width: 500px;
    }
    .summary__td, .summary__th {
      width: 40%;
      
      &:last-of-type {
        width: 20%;

        a:nth-child(n + 2) {
          margin-left: 30px;
        }
      }
    }
  }

  &_col-6 {
    .summary__table {
      min-width: 700px;
    }
    .summary__td, .summary__th {

      &:first-child {
        width: 22%;
      }

      &:nth-child(2), &:nth-child(4) {
        width: 15%;
      }

      &:nth-child(3) {
        width: 12%;
      }

      &:nth-child(n + 5) {
        width: 18%;
      }
      
      &:last-of-type {

        a:nth-child(n + 2) {
          margin-left: 15px;
        }
      }
    }
  }

  &_col-7 {
    .summary__table {
      min-width: 1000px;
    }
    .summary__td, .summary__th {

      &:first-child {
        width: 12%;
      }
      &:nth-child(2) {
        width: 17%;
      }

      &:nth-child(3) {
        width: 14%;
      }

      &:nth-child(4) {
        width: 17%;
      }

      &:nth-child(5) {
        width: 12%;
      }

      &:nth-child(6) {
        width: 14%;
      }

      &:nth-child(7) {
        width: 14%;
      }
    }
  }

  &_col-9 {
    .summary__table {
      min-width: 1000px;
    }
    .summary__td, .summary__th {

      &:first-child {
        width: 9%;
      }
      &:nth-child(2), &:nth-child(4) {
        width: 8%;
      }

      &:nth-child(3) {
        width: 17%;
      }

      &:nth-child(5) {
        width: 14%;
      }

      &:nth-child(6) {
        width: 10%;
      }

      &:nth-child(7) {
        width: 15%;
      }

      &:nth-child(8) {
        width: 12%;
      }

      &:nth-child(9) {
        width: 7%;
      }
    }
  }

  .sent {
    color: #219653;
  }

  .request {
    color: #F2994A;
  }

  .canceled {
    color: #EB5757;    
  }

  .paid {
    color: #2F80ED;
  }
}

@media (max-width: $breakpoint-medium) {

  .summary {
    &__table {
      margin-bottom: 15px;
    }
  }
}

@media (max-width: $breakpoint-small) {

  .summary {

  }
}