@import "../uikit-custom/uikit.scss";


//--------------------------------
// Import fonts
//--------------------------------
// @import "fonts";

//--------------------------------
// Global variables
//--------------------------------
$g-font: 'Roboto', sans-serif;
$g-font-extra: 'Open Sans', sans-serif; 

$g-col-green: #A3C156;
$g-col-l-green: #E7ECDA;
$g-col-green-hov: #86B946;

$g-col-text: #7D7D7D;
$g-col-l-gray: #F5F5F5;
$g-col-gray-strouk: #C4C4C4;

$g-col-blue: #11133F;
$g-col-black: #000;
$g-col-white: #fff;

$g-col-red: #EB5757;
$g-col-red-hov: #CC2424;

$g-shadow: 0px 12px 24px rgba(0, 0, 0, 0.06);
$g-anime: 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);


$font-sizes: (
	//			960  1200
	h1: 	(30px, 45px),
	h2:		(26px, 36px),
	h3:		(18px, 26px),
	h4:		(70px, 76px),
	h5:		(22px, 22px),
	p1:		(14px, 18px),
	p2:		(14px, 16px),
	p3:		(12px, 13px),
	p5:		(12px, 14px),
	p6:		(12px, 12px),
	p7:		(12px, 14px),
	p8:		(18px, 18px),
	p9:		(14px, 16px),
	p10:	(12px, 14px),
	p11:	(48px, 48px),
	p12:	(24px, 24px),
	p13:	(22px, 22px),
	p14:	(26px, 26px),
	p15:	(59px, 144px),
	p16:	(96px, 200px),
	p17:	(11px, 13px),
	p18:	(18px, 26px)
);

//--------------------------------
// Global breakpoints
//--------------------------------
$breakpoint-small:                              640px !default;  // Phone landscape
$breakpoint-medium:                             960px !default;  // Tablet Landscape
$breakpoint-large:                              1200px !default; // Desktop
$breakpoint-xlarge:                             1600px !default; // Large Screens

$breakpoint-default:                             1366px !default; // default width

//--------------------------------
// Global mixins
//--------------------------------
@mixin fluidFontSize($f-min, $f-def: $f-min, $w-def:$breakpoint-default, $w-min: $breakpoint-medium, $w-max: $breakpoint-default) {

	$f-new-def: $f-def - ($w-def / 200);
	font-size: calc(#{$f-new-def} + 0.5vw);

	@media (max-width: $w-min) {
		font-size: $f-min;
	}
	@media (min-width: $w-max) {
		font-size: $f-def;
	}
}

@mixin font-size($sizes) {
	$sizesList: map-get($font-sizes, $sizes);
	$newFont: nth($sizesList, 2) - ($breakpoint-default / 200);

	font-size: calc(#{$newFont} + 0.5vw);

	@media (max-width: $breakpoint-medium) {
		font-size: nth($sizesList, 1);
	}
	@media (min-width: $breakpoint-default) {
		font-size: nth($sizesList, 2);
	}
};

//--------------------------------
// Global style
//--------------------------------
::placeholder {
	color: $g-col-text;
}

::selection {
	background-color: $g-col-green;
	color: #fff;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
	overflow: hidden; }

body {
	position: relative;
	min-width: 320px;
	font-size: 16px;
	line-height: 1.2;
	font-family: $g-font;
	font-weight: 400;
	overflow-x: hidden;
	color: $g-col-text;
	background: $g-col-l-gray;

	@include font-size('p1');
	input, textarea {
		border: 1px solid $g-col-gray-strouk;
		outline: none;
		&:focus:required:invalid {
			// border-color: $g-col-red;
		}
		&:required:valid {
			// border-color: $g-col-green;
		}
	}
}

.container {
	max-width: $breakpoint-default;
	margin: 0 auto;
	padding-left: 30px;
	padding-right: 30px; }

.container-max {
	max-width: $breakpoint-default;
	margin: 0 auto; }


.text-center {
	text-align: center;
}

b, strong {
	font-weight: bold;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

a {
	color: inherit;
	transition: $g-anime;
	&:hover {
		text-decoration: none;
	}

	&:focus {
		outline: none;
	}

	&:before, &:after {
		transition: $g-anime;
	}
}

* { box-sizing: border-box; }

button {
	font-family: $g-font;
	font-size: inherit;
	font-weight: inherit;
	color: inherit;
	cursor: pointer;
	transition: $g-anime;

	&:before, &:after {
		transition: $g-anime;
	}
	&:focus {
		outline: none;
	}
}

input {
	width: 100%;
	padding-top: 12px;
	padding-bottom: 12px;
	padding-left: 30px;
	padding-right: 20px;
	font-family: $g-font;
	@include font-size('p1');
	border: 1px solid #C4C4C4;
	border-radius: 5px;
	color: $g-col-blue;
	background: $g-col-l-gray;
	letter-spacing: 0.01em;
	&::placeholder {
		font-size: inherit;
		color: $g-col-text;
	}
}

select, textarea {
	font-family: $g-font;
}

.btn {
	display: block;
	border: none;
	background: transparent;
	text-align: center;
	@include font-size('p1');
	letter-spacing: 0.01em;

	&_login {
		width: 120px;
		height: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		@include font-size('p10');
		font-weight: 500;

		color: $g-col-green;
		border: 1px solid $g-col-green;
		border-radius: 4px;

		&:hover {
			background-color: $g-col-green;
			color: #fff;
		}
	}

	&_burger {
		position: relative;
    width: 24px;
    height: 14px;
    padding: 0;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: $g-col-blue;
			box-shadow: 0 6px 0 0 $g-col-blue;
		}

		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: $g-col-blue;
		}

		&.uk-open {
      &:before {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        box-shadow: none;
      }
      
      &:after {
        top: 50%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%) rotate(45deg);
      }
    }
	}

	&_search {
    padding: 0;
    width: 30px;
    height: 30px;
    background: transparent;
    border: none;

    &:hover {
      opacity: 0.7;
		}
	}

	&_green {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 60px;
		color: $g-col-white;
		background-color: $g-col-green;
		border-radius: 4px;
		&:hover {
			color: $g-col-white;
			background-color: $g-col-green-hov;
		}
	}

	&_blue {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 60px;
		background-color: $g-col-blue;
		border: 1px solid $g-col-blue;
		border-radius: 4px;
		&:hover {
			background-color: transparent;
		}
	}

	&_red {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 60px;
		background-color: $g-col-red;
		border: 1px solid $g-col-red;
		border-radius: 4px;
		&:hover {
			background-color: $g-col-red-hov;
		}
	}

	&_exchange {
		padding: 0;
		height: 30px;
		width: 20px;
	}

	&_small {
		width: 55px;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 1px solid $g-col-green;
		color: $g-col-green;
		border-radius: 5px;
		@include fluidFontSize(12px, 13px);
		&:hover {
			border: 1px solid $g-col-green-hov;
			color: $g-col-green-hov;
		}
	}
}

.link {
	display: block;
	border: none;
	background: transparent;
	text-align: center;
	@include font-size('p1');
	letter-spacing: 0.01em;

	&_green {
    color: $g-col-green;
		&.arrow-right:after {
			border-color: $g-col-green;
		}

		&:hover {
			color: $g-col-white;
			&.arrow-right:after {
				border-color: $g-col-white;
			}
		}
	}

	&_green-ud-line {
		color: $g-col-green;
		text-decoration: underline;
		&:hover {
			color: $g-col-green-hov;
			text-decoration: none;
		}
	}

	&_blue {
		color: $g-col-blue;

		&.arrow-right:after {
			border-color: $g-col-blue;
		}

		&:hover {
			color: $g-col-green;
			&.arrow-right:after {
				border-color: $g-col-green;
			}
		}
	}

	&_blue-ud-line {
		color: $g-col-blue;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}

	&_white {
		color: $g-col-white;
		&.arrow-right:after {
			border-color: $g-col-white;
		}

		&:hover {
			color: $g-col-green;
			&.arrow-right:after {
				border-color: $g-col-green;
			}
		}
	}

	&_pen {

		.icon {
			fill: $g-col-blue;
			transition: $g-anime;
		}

		&:hover {
			.icon {
				fill: $g-col-green;
			}
		}
	}

	&_pen-pale {
		.icon {
			fill: $g-col-gray-strouk;
			transition: $g-anime;
		}

		&:hover {
			.icon {
				fill: $g-col-blue;
			}
		}
	}

	&_basket {
		.icon {
			fill: $g-col-red-hov;
			transition: $g-anime;
		}

		&:hover {
			.icon {
				fill: $g-col-green;
			}
		}
	}

	&_basket-pale {
		.icon {
			fill: $g-col-gray-strouk;
			transition: $g-anime;
		}

		&:hover {
			.icon {
				fill: $g-col-red-hov;
			}
		}
	}
}


.hide-content {
  display: none;
}

.more-elipses.show {
  display: none;
  & + .hide-content {
    display: inline;
  }
}

.btn-show-more {
  margin-left: 10px;
  font-weight: 500;
  font-size: 14px;
  // color: $g-color;
  text-decoration-line: underline;
	cursor: pointer;
	transition: $g-anime;

	&:hover, &:focus {
		// color: $g-link-color;
	}
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}

//--------------------------------
// here --> accord-arrow
//--------------------------------
.arrow-up, .arrow-right, .arrow-left, .arrow-down {
  position: relative;
  display: inline-block;
  padding-right: 20px;

  &:after {
    position: absolute;
    content: "";
    top: 50%;
    right: 3px;
    border: solid $g-col-blue;
    border-width: 2px 2px 0 0;
    padding: 3px;
    transition: $g-anime;
		transform: translate(0, -85%) rotate(135deg);	
  }

  &.uk-open, &.active {
    &:after {
			transform: translate(0, -50%) rotate(-45deg);
    }
  }
}

.uk-open > .arrow-up:after, .active > .arrow-up:after {
	transform: translate(0, -50%) rotate(-45deg);	
}

.arrow-right {
	&:after {
		border-width: 2px 0 0 2px;
		transform: translate(0, -50%) rotate(135deg);	
  }
}

.arrow-left {
	padding-right: 0;
	padding-left: 20px;
	&:after {
    right: auto;
		left: 3px;
		border-width: 0 2px 2px 0;
		transform: translate(0, -50%) rotate(135deg);
  }
}


//--------------------------------
// here --> accord-arrow
//--------------------------------
.pointer {
	position: relative;
	display: inline-block;
	width: 21px;
	height: 20px;

	&:hover {
		opacity: 0.7;
	}

	&:before {
		position: absolute;
		content: "";
		top: 50%;
		transform: translateY(-50%);
		width: 20px;
		height: 2px;
		background-color: #fff;
	}

	&:after {
		position: absolute;
    content: "";
    top: 50%;
    border: solid $g-col-white;
    padding: 3px;
    transition: $g-anime;
		transform: translate(0, -50%) rotate(135deg);	
	}

	&_left {
		&:before {
			left: 1px;

		}

		&:after {
			left: 0;
			border-width: 0 2px 2px 0;
		}
	}

	&_right {
		&:before {
			right: 1px;
		}

		&:after {
			right: 0;
			border-width: 2px 0 0 2px;
		}
	}
}


.triangle {
	position: relative;

	&:after {
		position: absolute;
		content: "";
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		transition: $g-anime;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 12px solid $g-col-blue;
	}
}

.uk-open .triangle {
	&:after {
		border-top: none;
		border-bottom: 12px solid $g-col-blue;
	}
}
//--------------------------------
// here --> icon cross
//--------------------------------
.cross {
	position: relative;
	&:before, &:after {
		position: absolute;
		content: "";
		top: 50%;
		right: 0;
		transform: translateY(-60%) rotate(-45deg);
		width: 10px;
		height: 2px;
		background: #fff;
	}

	&:after {
		transform: translateY(-60%) rotate(45deg);
	}
}


@media (max-width: $breakpoint-large) {

	.btn {
		&_login {
