.p-about {
  margin-top: 60px;
  margin-bottom: 120px;
  &__img {
    position: relative;
    margin-bottom: 60px;
    border-radius: 5px;
    overflow: hidden;
  }

  &__wrap {
    position: absolute;
    left: 4%;
    top: 21%;
    color: $g-col-white;
  }

  &__subtitle {
    margin-bottom: 15px;
    @include font-size('p1');
    letter-spacing: 0.01em;
  }

  &__title {
    @include fluidFontSize(36px);
    font-weight: 700;

    p {
      margin: 0;
    }
  }

  &__text {
    @include font-size('p1');
    letter-spacing: 0.01em;
    line-height: 1.5;
  }
}

@media (max-width: $breakpoint-medium) {
  .p-about {
    &__img {
      margin-bottom: 0;
      &:before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(17, 19, 63, 0.6), rgba(17, 19, 63, 0.6));
      }
      picture {
        display: flex;
        max-height: 300px;
      }
      img {
        object-fit: cover;
        width: 100%;
      }
    }

    &__content {
      flex-direction: column-reverse;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .p-about {
    margin-top: 15px;
    margin-bottom: 90px;
    padding-left: 20px;
    padding-right: 20px;
    &__img {
      margin-bottom: 30px;
    }

    &__wrap {
      top: 65px;
      left: 25px;
    }

    .support {
      margin-right: 30px;
    }
  }
}