.headline {
  margin-top: 25px;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: space-between;

  &__hist {
    margin-top: 10px;
    display: flex;
    @include fluidFontSize(10px, 13px);
    font-family: $g-font-extra;
  }

  &__title {
    margin: 0;
    @include font-size('h2');
    font-weight: 700;
    color: $g-col-blue;
  }

  &__user {
    margin: 0;
    margin-right: 30px;
  }

  &__date {
    margin: 0;
  }

  span, time {
    color: $g-col-blue;
    font-weight: 700;
  }

}

@media (max-width: $breakpoint-medium) {

  .headline {
    &__user {
      margin-right: 10px;
    }
  
  }
}

@media (max-width: $breakpoint-small) {
  .headline {
    display: block;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;

    &__hist {
      display: block;
    }
  }
}