.description {
  margin-top: 90px;
  margin-bottom: 50px;
  &__title {
    margin-bottom: 30px;
    @include font-size('h3');
    font-weight: 700;
    color: $g-col-blue;
  }

  &__wrap {
    line-height: 1.5;
    @include font-size('p1');
    letter-spacing: 0.01em;
  }
}

@media (max-width: $breakpoint-medium) {
  .description {
    &__title {
      margin-bottom: 15px;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .description {
    margin-top: 60px;
    margin-bottom: 100px;
    padding-left: 20px;
    padding-right: 20px;
  }
}