.benefits {
  margin-top: 90px;
  margin-bottom: 90px;
  color: $g-col-blue;

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    margin-bottom: 30px;
    border-radius: 50%;
    background-color: $g-col-green;
  }

  .icon {
    fill: $g-col-white;
  }

  &__wrap {
    position: relative;
    &:before {
      position: absolute;
      content: "";
      z-index: 0;
      bottom: 0;
      left: 0;
      width: 100px;
      height: 10px;
      background-color: $g-col-l-green;
    }
  }

  &__title {
    position: relative;
    z-index: 1;
    @include font-size('h3');
    font-weight: 700;
  }
}

@media (max-width: $breakpoint-medium) {
  .benefits {

    &__list > .uk-grid-margin {
      margin-top: 60px;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .benefits {
    margin-top: 50px;
    margin-bottom: 60px;
    padding-left: 20px;
    padding-right: 20px;
    &__img {
      margin-left: 20px;
      width: 60px;
      height: 60px;
    }

    .icon {
      width: 30px;
      height: 30px;
    }
  }
}